<template>
  <div>
    <button
      type="button"
      @click="$router.go(-1)"
      class="btn btn-sm btn-style1 font-bold text-uppercase underline"
      style="position:absolute;z-index:100;"
    >
      <span class="text-xl typcn typcn-arrow-left"></span> Kembali
    </button>
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320" class="animated fadeInDown">
      <path
        fill="#0099ff"
        fill-opacity="1"
        d="M0,256L60,256C120,256,240,256,360,229.3C480,203,600,149,720,106.7C840,64,960,32,1080,69.3C1200,107,1320,213,1380,266.7L1440,320L1440,0L1380,0C1320,0,1200,0,1080,0C960,0,840,0,720,0C600,0,480,0,360,0C240,0,120,0,60,0L0,0Z"
      />
    </svg>
    <div class="card animate__animated animate__zoomIn">
      <div class="card-header">History Ijin Keluar</div>
      <div class="card-body">
         <a :href="`https://akbarmaliki.github.io/infolayanansjs/#/excel?data=${JSON.stringify(susun(td)).split('&').join('%26')}`" > <button type="button" class="btn btn-sm btn-success  float-right"><span class="typcn typcn-printer"></span>  <span class="typcn typcn-chart-bar"></span></button></a>
          <div class="sm-form ">
              <label for="search">Cari Tanggal</label>
              <input type="text" id="search" name="search" class="form-control form-control-sm" placeholder="search" v-model="search" >
          </div>
          <div class="row">
            <div class="col-6">
              Lihat data terakhir: 
            </div>
            <div class="col-6">
              <select class='form-control' v-model="jumlah">
              <option value="5">5</option>
              <option value="10">10</option>
              <option value="9999">Semua</option>
              </select>

            </div>
          </div>
        <div
        @click="$router.push(`/lembur/history/detail?id=${item.id}&uid=${item.uid}`)"
          v-for="(item, index) in td"
          :key="index+'datanya'"
          class="p-2 rounded-lg shadow-1 bg-gray-200 mt-1"
        >
       
          <tr>
            <td>NIP</td>
            <td class="pl-1 pr-1">&nbsp;:&nbsp;</td>
            <td>{{item.nip}}</td>
          </tr>
           <img
            v-if="item.gambar!='-'"
            :src="item.gambar"
            v-viewer
            class="kinoLightBox img-fluid rounded-circle float-right"
            style="margin-top:20px;width:40px;height:40px;"
          />
          <img
            v-else
            src="@/static/nofound.png"
            v-viewer
            class="kinoLightBox img-fluid rounded-circle float-right"
            style="margin-top:20px;width:40px;height:40px;"
          />
          Nama
          &nbsp;:&nbsp;
          {{item.nama}}
          <br />
          Unit
          &nbsp;:&nbsp;
          {{item.unit_kerja}}
         <tr>
            <td>Tanggal Ijin Keluar</td>
            <td class="pl-1 pr-1">&nbsp;:&nbsp;</td>
            <td>{{format(item.tanggal_ijin)}}</td>
          </tr>
           <tr v-show="item.lama_keluar.indexOf('undefined')==-1">
            <td>Lama Ijin Keluar</td>
            <td class="pl-1 pr-1">&nbsp;:&nbsp;</td>
            <td>{{item.lama_keluar}} Jam</td>
          </tr>
            <tr>
            <td>Ijin</td>
            <td class="pl-1 pr-1">&nbsp;:&nbsp;</td>
            <td class="font-bold">{{item.ijin}}</td>
          </tr>
          <tr>
            <td>Keperluan</td>
            <td class="pl-1 pr-1">&nbsp;:&nbsp;</td>
            <td>{{item.keperluan}}</td>
          </tr>
          <button type="button" class="btn btn-sm " :class="item.posisi?'btn-success':'btn-danger'">{{item.posisi ? 'Dikantor':'Diluar'}}</button>
          <span class="text-sm float-right">{{item.createAt?distanceToNow(item.createAt):''}} Yang lalu</span>
          <br />
          <br />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
var id = require("date-fns/locale/id");
import axios from "axios";
import firebase from "firebase";
let db = firebase.firestore();
import autophp from '@/plugins/autophp.js'
let sdb = new autophp()

export default {
  data() {
    return {
      datanya: [],
      search:"",
      jumlah:5
    };
  },
  methods: {
    distanceToNow(date) {
      return this.$datefns.distanceInWordsToNow(new Date(date), {
        locale: id,
        includeSeconds: true
      });
      //=> "3 days ago"
    },
      getData(){
 let that= this
   sdb.collection("ijin_keluar").doc().select(`
   select tb2.nama as nama,tb2.gambar as gambar,tb1.lama_keluar,tb2.nip as nip,tb2.unit as unit,tb1.tanggal_ijin as tanggal_ijin,tb1.ijin as ijin,
   tb1.keperluan as keperluan,tb1.createAt as createAt,tb1.status as status, tb1.id AS id, tb2.id as id2
   from ijin_keluar tb1 left join tbuser tb2 on tb2.id=tb1.froms where tb2.skpd='${this.$store.state.users.skpd}'`).then(res => {
     console.log('datanya',res)
        let data = res 
        data = data.map(e => {
          return {
            id: e.id,
            id2: e.id2,
            ...e,
            tanggalnya:this.format(e.tanggal_ijin)
          };
        });
        data = data.filter(e => {
            if (e.status == 3) {
                return e;
          }
        });
        // data = data.filter(e=>{
        //   if(e.froms==that.$store.state.users.id){
        //     return e;
        //     }
        // })
        console.log('datanya',data)
        data = this.$_.orderBy(data, ['createAt'],['desc']);
        this.datanya = data;
        console.log(data);
    });
    },
    format(date) {
      return this.$datefns.format(new Date(date), "DD MMMM YYYY", {
        locale: id
      });
    },
      susun(datas){
      let that=this
      let data = datas;
      // let order = ['id','nip','nama','unit','tujuan','keperluan','lokasi','jam_berangkat','driver','mobil','createAt','kendaraan']
      let hasil=[]
      let obj={}
      // console.log('susun',data)
      let datanya = data.map((e)=>{
        let obj={}
        console.log(e)
        obj.nip=e.nip
        obj.nama=e.nama
        // obj.unit=e.unit_kerja
        obj.lama_keluar=e.lama_keluar
        obj.tanggal_ijin=e.tanggal_ijin
        obj.jenis_pekerjaan=e.jenis_pekerjaan
        obj.keperluan=e.keperluan
        obj.ijin=e.ijin
        return obj
      })
      return datanya
    },
  },
  computed:{
      td(){
          let that = this
          let data = this.datanya 
          data=data.slice(0,this.jumlah);
          data=data.filter(e=>{
            if(e.tanggalnya || e.nama || e.nip){
                  if(e.tanggalnya.toLowerCase().indexOf(that.search.toLowerCase())!=-1 || e.nama.toLowerCase().indexOf(that.search.toLowerCase())!=-1 || e.nip.toLowerCase().indexOf(that.search.toLowerCase())!=-1){
                    return e
                  }
                }
          })
        
          return data
      }
  },
  mounted() {
    this.getData()
    // db.collectionGroup(`history-lembur`)
    //   .get()
    //   .then(res => {
    //     let data = res.docs.map(e => {
    //       return {
    //         id: e.id,
    //         ...e.data(),
    //         tanggalnya:this.format(e.data().tanggal_ijin)
    //       };
    //     });
    //     data = this.$_.orderBy(data, ['createAt'],['desc'])
    //     this.datanya = data;
    //     console.log('data',data);
    //   });
  },
  layout: "lembur"
};
</script>